 */
.picker__input.picker__input--active {
  border-color: #0089ec;
}
/**
 * The holder is the only scrollable top-level container element.
 */
.picker__holder {
  width: 100%;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

