/* copy the popover code from bootstrap so this will work even without it */
.popover {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1060;
    display: none;
    max-width: 276px;
    padding: 1px;
    font-size: 14px;
    font-weight: normal;
    line-height: 1.42857143;
    text-align: left;
    white-space: normal;
    background-color: #fff;
    -webkit-background-clip: padding-box;
            background-clip: padding-box;
    border: 1px solid #ccc;
    border: 1px solid rgba(0, 0, 0, .2);
    border-radius: 6px;
    -webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
            box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
}
.popover.top {
    margin-top: -10px;
}
.popover.bottom {
    margin-top: 10px;
}
.popover-title {
    padding: 8px 14px;
    margin: 0;
    font-size: 14px;
    background-color: #f7f7f7;
    border-bottom: 1px solid #ebebeb;
    border-radius: 5px 5px 0 0;
}
.popover-content {
    padding: 9px 14px;
}
.popover > .arrow,
.popover > .arrow:after {
    position: absolute;
    display: block;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
}
.popover > .arrow {
    border-width: 11px;
}
.popover > .arrow:after {
    content: "";
    border-width: 10px;
}
.popover.top > .arrow {
    bottom: -11px;
    left: 50%;
    margin-left: -11px;
    border-top-color: #999;
    border-top-color: rgba(0, 0, 0, .25);
    border-bottom-width: 0;
}
.popover.top > .arrow:after {
    bottom: 1px;
    margin-left: -10px;
    content: " ";
    border-top-color: #fff;
    border-bottom-width: 0;
}
.popover.bottom > .arrow {
    top: -11px;
    left: 50%;
    margin-left: -11px;
    border-top-width: 0;
    border-bottom-color: #999;
    border-bottom-color: rgba(0, 0, 0, .25);
}
.popover.bottom > .arrow:after {
    top: 1px;
    margin-left: -10px;
    content: " ";
    border-top-width: 0;
    border-bottom-color: #fff;
}

/* Structure */
user-friendly-json-tree {
  box-sizing: border-box;
}
user-friendly-json-tree *,
user-friendly-json-tree *:before,
user-friendly-json-tree *:after {
  box-sizing: border-box;
}
user-friendly-json-tree .key {
  vertical-align: middle;
}
user-friendly-json-tree .expandable {
  position: relative;
}
user-friendly-json-tree .expandable::before {
  pointer-events: none;
}
user-friendly-json-tree .expandable::before,
user-friendly-json-tree .expandable > .key {
  cursor: pointer;
}
user-friendly-json-tree .branch-preview {
  display: inline-block;
  vertical-align: middle;
}
/* Looks */
user-friendly-json-tree ul {
  padding-left: 20px;
}
user-friendly-json-tree li,
user-friendly-json-tree ul {
  list-style: none;
}
user-friendly-json-tree li {
  line-height: 1.5em;
}
user-friendly-json-tree .key {
  color: #007AFF;
  font-weight: bold;
  padding: 5px 10px 5px 15px;
}
user-friendly-json-tree .key::after {
  content: ':';
}
user-friendly-json-tree user-friendly-json-node.expandable::before {
  content: '\25b6';
  position: absolute;
  left: 0px;
  font-size: 10px;
  transition: -webkit-transform .1s ease;
  transition: transform .1s ease;
  transition: transform .1s ease, -webkit-transform .1s ease;
}
user-friendly-json-tree user-friendly-json-node.expandable.expanded::before {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
user-friendly-json-tree .leaf-value,
user-friendly-json-tree .branch-preview {
  word-break: break-all;
}
user-friendly-json-tree .branch-preview {
  overflow: hidden;
  font-style: italic;
  max-width: 40%;
  height: 1.5em;
  opacity: .7;
}

